@font-face {
  font-family: 'icomoon';
  src:  url('./icomoon.eot?c10sqw');
  src:  url('./icomoon.eot?c10sqw#iefix') format('embedded-opentype'),
    url('./icomoon.ttf?c10sqw') format('truetype'),
    url('./icomoon.woff?c10sqw') format('woff'),
    url('./icomoon.svg?c10sqw#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

